import axios from 'axios';

export const loadAlgorithms = async () => {
    const res = await axios.get('/algorithms/list');
    return {'algorithms': res.data};
};

export const loadAlgorithm = async (algorithm) => {
    const res = await axios.get('/algorithms/' + algorithm);
    return {'algorithm': res.data};
};

export const runAlgorithm = async (algorithm, fun, args) => {
    try {
        const res = await axios.post('/algorithms/' + algorithm, {'function': fun, args});
        return {
            'result': {
                [algorithm + ':' + fun]: res.data
            }
        };

    } catch (e) {
        return {};
    }
};


