import React from 'react';

export default (props) => {

    const {result, time} = props.data;

    const data =
        !Array.isArray(result) ?
            <div>{result}</div> :
            result.map(it => {
                let internal = it;
                if (Array.isArray(it)) internal = it.join(' , ');
                return <div key={it} className="part">{internal}</div>
            });

    return <div className="result-container">
        <h4>Results</h4>
        <small className="time">{time}</small>
        <div className="result">
            {data}
        </div>
    </div>

}



