import React, {Component} from 'react';
import {loadAlgorithm, loadAlgorithms, runAlgorithm} from "./api";
import {FormControlLabel, Switch, TextField} from "material-ui";
import Result from "./Result";

class App extends Component {

    state = {
        cache: {}
    };

    async componentDidMount() {
        this.setState(await loadAlgorithms());
    }

    async selectAlgorithm(active) {
        this.setState({active});
        const {cache} = this.state;
        if (cache[active]) return this.setState({algorithm: cache[active]});
        const alg = await loadAlgorithm(active);

        for (let fun in alg.algorithm) {
            alg.algorithm[fun].forEach(field =>
                field[1] = field[1].replace(/<class '|'>|typing\.|]/g, '').toLowerCase().split('[')
            );
        }

        this.setState({...alg, cache: {[active]: alg.algorithm}});

    }

    async run(fun) {
        const {active, algorithm} = this.state;
        const args = algorithm[fun].map(it => it[1][0] === 'int' ? parseInt(it[2]) : it[1][0] === 'list' ? it[2].split(',').map(it => parseInt(it.trim())) : it[2] || null);
        this.setState({fun});
        this.setState(await runAlgorithm(active, fun, args));
    }

    updateField(field, value) {
        const algorithm = this.state.algorithm;
        field[2] = value;
        this.setState({algorithm});
    }

    render() {

        const clear = (value) => value.replace(/_/g, ' ');

        const {algorithms = [], algorithm = {}, active, result = {}} = this.state;

        const sidebar = algorithms.map(it =>
            <li key={it[1]}
                onClick={() => this.selectAlgorithm(it[1])}
                className={active === it[1] ? 'active' : ''}>
                <span>{it[0]}</span>
                {active === it[1] && <div/>}
            </li>);

        const check = (fun) => result[`${active}:${fun}`];

        const cards = Object.entries(algorithm).map(it => <div key={it[0]} className="card">

            <h4>{clear(it[0])}</h4>

            {

                it[1].map(field => {

                    if (['str', 'int', 'list', 'set', 'dict', 'tuple'].includes(field[1][0]))
                        return <TextField
                            key={field[0]}
                            margin="dense"
                            label={clear(field[0])}
                            value={field[2]}
                            onChange={e => this.updateField(field, e.target.value)}/>;

                    if (field[1][0] === 'bool')
                        return <FormControlLabel
                            key={field[0]}
                            label={clear(field[0])}
                            control={
                                <Switch
                                    value={field[2]}
                                    onChange={e => this.updateField(field, e.target.checked)}/>
                            }/>

                })

            }


            <div className="actions">
                <div className="button" onClick={() => this.run(it[0])}>RUN</div>
            </div>

            {check(it[0]) !== undefined && <Result data={check([it[0]])}/>}

        </div>);


        return (
            <div>
                <div className="content">
                    {cards}
                </div>
                <div className="sidebar">
                    {sidebar}
                </div>
            </div>
        );
    }

}

export default App;



